import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'

import Modal from 'antd/es/modal'
import Activity from './Activity'
import Post from '../../components/Post/Post'
import AddPost from '../../components/Post/AddPost'
import pullData from '../../utils/pullData'
import Layout from '../../components/Layout/Layout'
import {
    CardActionArea,
    CardContent,
    CardMedia,
    createStyles,
    makeStyles,
    Typography,
} from '@material-ui/core'
import axios from 'axios'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import { useTranslation } from 'react-i18next'
import { getPH, placeholderImages } from '../../components/MasonryCard'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const Progress = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [state, setState] = useState({})
    const [similarProgresses, setSimilarProgresses] = useState([])
    const [similarIsRequested, setSimilarIsRequested] = useState(false)

    const [data, setData] = useState({})
    const pathParts = props.history.location.pathname.split('/')
    const progressId = pathParts.length > 2 ? pathParts[2] : ''
    if (!progressId) return null
    useEffect(() => {
        if (props.isAuthed) {
            props.setData({ currentProgress: progressId })
            if (props.account._id)
                wsActions.addRecentProgress(props.account._id, progressId)
        } else {
            pullData(props, data, setData, { forceAuth: false }, data => {
                const friendData = {}
                for (let friend of data.friendData)
                    friendData[friend._id] = friend
                const post = {}
                for (let item of data.posts) post[item._id] = item

                props.setData({
                    friendData,
                    post,
                    account: {},
                    progress: data.progress,
                })
            })
        }
    }, [props.isAuthed, props.history.location.pathname])

    const { progress, account, friendData, posts } = props.isAuthed
        ? props
        : { ...data, posts: props.posts, account: {} }
    useEffect(() => {
        if (progress && progress._id && !similarIsRequested) {
            setSimilarIsRequested(true)
            axios
                .post('/api/goals/similar', {
                    search: {
                        categories: progress.category || [],
                        position: progress.position,
                        currentId: progress._id,
                    },
                })
                .then(res => {
                    if (res.data.success) {
                        setSimilarProgresses(res.data.progresses)
                    }
                })
                .catch(err => {})
        }
    }, [progress && progress._id])

    if (!progress || !progress._id) return null

    props.setData({ currentPosts: progress.posts })

    return friendData ? (
        <Layout
            header={
                <Typography
                    variant="h6"
                    className={classes.title}
                    color="inherit"
                    noWrap
                >
                    {progress.name}
                </Typography>
            }
        >
            <div className="contentDiv">
                <Activity
                    state={state}
                    setState={setState}
                    progress={progress}
                    account={account}
                    friendData={friendData}
                />

                {similarProgresses.length > 0 && (
                    <HorizontalScrollList
                        title={t('g.similarGoals')}
                        items={similarProgresses}
                        content={item => (
                            <CardActionArea>
                                <Link to={'/goals/' + item._id}>
                                    <CardMedia
                                        style={{
                                            height: '150px',
                                            backgroundSize: 'contain',
                                        }}
                                        image={
                                            item.images.length > 0
                                                ? item.images[0]
                                                : placeholderImages[
                                                      getPH(item._id)
                                                  ]
                                        }
                                        title={item.name}
                                    />
                                    <CardContent style={{ padding: '5px' }}>
                                        <Typography variant="body2" noWrap>
                                            {item.name}
                                        </Typography>
                                    </CardContent>
                                </Link>
                            </CardActionArea>
                        )}
                    />
                )}

                {account._id && (
                    <AddPost
                        parent={progressId}
                        account={account}
                        parentType="progress"
                        // onSend={() => setEditingMessage('')}
                    />
                )}
                {posts &&
                    [...progress.posts]
                        .sort((a, b) =>
                            props.history.location.pathname.indexOf(a) > -1
                                ? 1
                                : props.history.location.pathname.indexOf(b) >
                                  -1
                                ? -1
                                : 0
                        )
                        .map(postId => {
                            if (posts[postId]) {
                                const post = posts[postId]
                                return (
                                    <div
                                        key={postId}
                                        style={{ paddingTop: '10px' }}
                                    >
                                        <Post
                                            friendData={friendData}
                                            account={account}
                                            post={post}
                                        />
                                    </div>
                                )
                            } else return null
                        })
                        .reverse()}

                <Modal
                    visible={state.previewImage}
                    footer={null}
                    onCancel={() => {
                        setState({ ...state, previewImage: '' })
                        document.body.removeAttribute('style')
                    }}
                >
                    <img
                        alt="example"
                        style={{ width: '100%' }}
                        src={state.previewImage}
                    />
                </Modal>
            </div>
        </Layout>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        progress: state.progress,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Progress)
)
